import { Component } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-tipounidad-create',
  templateUrl: 'tipounidad-create.html',
})
export class TipounidadCreatePage {

  public form                  : FormGroup;
  username: string = "";
  public nombre                 : any;
  public codigo		     : any;
  public estado		     : any;
  public estado2		     : any;
  public iddepartamento		  :any;
  public iddepartamento2		  :any=[];
  public msg		     : any;
  idempresa:any;
  public abreviatura		     : any;
  public vgrupo  :any=[];

  private _ID                  : String;
  public pageTitle           	: string;

  constructor(public storage: Storage,public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
     
  })
    
     this.form = this._FB.group({
       nombre     : ['', Validators.required],
       codigo    : [''],
       iddepartamento     : ['', Validators.required],
       abreviatura     : [''],
       msg     : [''],
       estado     : ['', Validators.required],
       estado2     : ['', Validators.required]
      
       
     });

      
  this.authService.getReg('todos','departamentos').then((result) => {
    this.vgrupo=result;
  }, (err) => {  
  this.msj.showError3('Error',err.error);
});
    
  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }

  ionViewDidLoad() : void
  {
    if(this.navParams.get("record")==null)
    {

    
      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
    else {
       this._ID 				=	this.navParams.data.record._id;
       this.nombre		=	this.navParams.data.record.nombre;
       this.codigo		=	this.navParams.data.record.codigo;
       this.estado		=	this.navParams.data.record.estado;
       this.estado2		=	this.navParams.data.record.estado2;
       this.msg		=	this.navParams.data.record.msg;
       this.iddepartamento		=	this.navParams.data.record.iddepartamento.id + ','+  this.navParams.data.record.iddepartamento.nombre;
       this.iddepartamento2=this.iddepartamento.split(',');
       this.abreviatura		=	this.navParams.data.record.abreviatura;
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
  }

  

  manageGallery() : void
  {
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';    
    }
     let   options       : any	     = { 
     nombre : this.form.controls['nombre'].value,
     codigo: this.form.controls['codigo'].value,
     iddepartamento: {id:this.iddepartamento2[0],nombre:this.iddepartamento2[1]},
     abreviatura: this.form.controls['abreviatura'].value,
     estado: this.form.controls['estado'].value,
     estado2: this.form.controls['estado2'].value,
     msg: this.form.controls['msg'].value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' tipo unidad '}
    };
      

     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'tipounidads').then((result) => {
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {  
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'tipounidads').then((result) => {
           if (!this.form.valid) { return; }
        //   console.log(result)
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  onSelectChange(selectedValue: any) {

    

  // this.iddepartamento=aa[0]
    this.iddepartamento2=selectedValue.split(',');

      }
  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }




}



